  
<template>
<el-form class="cf" ref="form" :model="form" label-width="100px">
  <el-form-item label="">
      <el-radio-group v-model="radio4" size="medium"  @change="changetype">
      <el-radio-button label="searchcart" ></el-radio-button>
      <el-radio-button label="cart"></el-radio-button>
    </el-radio-group>
  <el-form-item label="" style="float:right;">
    <el-switch v-model="form.delivery"></el-switch>
  </el-form-item>

  </el-form-item>
  <el-form-item label="">
     <el-date-picker
      v-model.lazy="date"
      :editable="false"
      :clearable="false"
      @change="datechange"
      type="daterange"
      :picker-options="pickerOpt"
      range-separator=""
      start-placeholder=""
      end-placeholder="">
    </el-date-picker>

 <p><span class="num">{{day}}</span>&nbsp;&nbsp;<span class="num">{{totalnum}}</span>&nbsp;&nbsp;<span class="num">{{totaltask}}</span></p>
  </el-form-item>

  <el-form-item label="">
    <el-input v-model="taskname" placeholder="()" ></el-input>
  </el-form-item>
  <el-form-item label="" >
    <el-input  v-if="radio4!='shop'" v-model="link" placeholder=")" ></el-input>
    <el-input  v-else   v-model="link" placeholder="" ></el-input>
  </el-form-item>




<template v-if="radio4=='searchcart'"   >
  <Task     ref="task"  v-for="(k,index) in keywordlist"  :keywordlistLength="keywordlist.length" v-on:myinc="inc" v-on:mynum="mynum"  v-on:mydec="dec"  :key="k"  :mykey="index"></Task>
  </template>
<template v-else>
<el-form-item label="">
    <el-input-number v-model="totalnum"  :min="1"  label=""></el-input-number>
</el-form-item>
</template>


<template v-if="form.delivery">
  

 <el-form-item label="">
    <el-input-number v-model="viewtime"   @change="myviewtime"  :min="30"  :step="10" label=""></el-input-number>
    <el-checkbox style="margin-left:15px;" v-model="checked"></el-checkbox>
</el-form-item>

  
 <el-form-item label="" class='cf'>
   <el-select v-model="mydeep" placeholder=""  >
    <el-option
      v-for="item in deep"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <span style="float:right;"></span>
 <el-input-number  style="float:right;" v-model="deeptime"  @change="mydeeptime"  :min="30" :step="10" label=""></el-input-number>

</el-form-item>
  
</template>



<el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="30%">
  <span>{{dialogMsg}}</span>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
</el-dialog>


<el-card class="box-card cf" style="margin-top:30px;">
    <p><span class="num">{{totaltime}}</span>&nbsp;&nbsp;<span class="num">{{perint}}</span>&nbsp;&nbsp;<span class="num">{{totalint}}</span></p>
    <el-button  @click="submit" style="float:right;" type="danger"></el-button>
  </el-card>

</el-form>





</template>

<script type="text/javascript">
import Task from '../Task.vue'
export default {
    data() {
      return {
           form:{},
           taskname:"",     //
           link:"",         // 
           radio4: 'searchcart',   //
           totalnum:100,    //
           date:[Date.now(),Date.now()],         //
           day:1,           //
           checked:false,   //
           viewnum:100,     //
           keywordlist:[0], //
           deeptime:30,     //
           viewtime:30,     //
           truedeeptime:0,   //
           mydeep:"0",      //
           deep:[
            {
              label:"", 
              value:"0",
            },
                        {
              label:"0~130",
              value:"1",
            },
                        {
              label:"0~224",
              value:"2",
            },
                        {
              label:"0~336",
              value:"3",
            },
                        {
              label:"130",
              value:"4",
            },
                        {
              label:"248",
              value:"5",
            },
            {
              label:"372",
              value:"6",
            },
           ],
           pickerOpt:{
            disabledDate:function(date){
              var  today = new Date();
              today.setHours(0);
              today.setMinutes(0);
              today.setSeconds(0);
              today.setMilliseconds(0);
               if(date.getTime()-today.getTime()>(24*3600*1000*31)||date.getTime()-today.getTime()< 0){
                return true;
               }else{
                return false;
               }
            }
           },
           dialogVisible:false,
           dialogMsg:""
      }
    },
    props:[],
    components:{
     Task
    },
    computed:{
      totaltask:function(){    //
        return this.day*this.totalnum
      },
      totaltime:function(){
      return this.viewtime+this.truedeeptime
    },
     perint:function(){
       switch(this.radio4){
          
       case "searchcart":
          return Math.round((this.totaltime/5)*2.5*0.3)+56;
       case "cart" :
          return Math.round((this.totaltime/5)*2.5*0.3)+47; 
       }
    },
    totalint:function(){
       return  this.totaltask*this.perint;
    }

    },
    watch:{
     mydeep:function(d){
      if(d!=0){
    
         this.truedeeptime = this.trandeeptime()
      }else{
         this.truedeeptime = 0
      }
     },
     radio4:function(newr,oldr){

     }
    },

    methods: {
    
       subdata() {
       var tasks = this.$refs.task;
       var len = tasks.length;
       var keywords = []
        if(this.link==""){
          this.showInfo("")
          return false;
        }
        if(len > 0){
            for(var i=0;i<len;i++){
          if(tasks[i].keyword==""){
          this.showInfo("")
          return false;
          }
          keywords.push({
            keyword:tasks[i].keyword,
            num:tasks[i].num,
            period:tasks[i].period
          })
        }
        }else{
          keywords = false;
        }
     
        var mydate = new Date(this.date[0]);
        return {
         link:this.link,
         date: (mydate.getTime())/1000,
         keywords:keywords,
         deeptime:this.deeptime,     
         viewtime:this.viewtime,     
         mydeep:this.mydeep, 
         radio4:this.radio4,
         checked:this.checked,
         taskname:this.taskname
        }
      },
      submit:function(){

    this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {


      var res =  this.subdata()

      if(!res){
        return;
      }
      res['totalnum'] = this.totalnum;  //
      res['totaltime'] =  this.totaltime;
      res['totalint']  =  this.totalint;
      const loading = this.$loading({    // loading
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        this.$http.post("/customer/index/addorder", res).then(response => {
          var res = response.body;
          loading.close();
          if(res.status==1){
              this.$alert(""+res.total+""+res.success+""+res.error+"", '', {
          confirmButtonText: '',
          callback: action => {
            this.$router.push({ path: '/order' })
          }
         });
        }else{
          this.$message({message:res,type:"error"})
        }
      
         }, response => {
          // error callback
         });
        

        }).catch(() => { 
         loading.close();        
        });

  
   
     },
      mynum(num){
         this.totalnum +=num;

      },
    //  myviewtime(time){
    //    this.$emit("addviewtime",time)
    //  },
      mydeeptime(time){
        if(this.mydeep!="0"){
        this.truedeeptime = this.trandeeptime()
        }
    },
      changetype(type){
        if(type=="search"){
          this.rnum()
        }
       //  this.$emit("changetype",type)
        
      },
      datechange(date){     //  date:array (min,max)
      console.log(date)
        var  dateSpan = date[1] - date[0];
       // var  dateSpan = Math.abs(dateSpan);
        var  iDays = Math.floor(dateSpan / (24 * 3600 * 1000));
        this.day = iDays+1      //
      },
      dec:function(index){ //
        this.keywordlist.splice(index,1)
      },
      inc:function(){  //

       this.keywordlist.push(this.keywordlist.length)
       return true;
      },
      trandeeptime:function(){  //  datadeep

          switch(this.mydeep){
            case "1":
                 return Math.ceil((1/2)*this.deeptime);
            case "2":
                 return Math.ceil((2/2)*this.deeptime);
            case "3":
                 return Math.ceil((3/2)*this.deeptime);       
            case "4":
                 return Math.ceil(1*this.deeptime); 
            case "5":
                 return Math.ceil(2*this.deeptime); 
            case "6":
                 return Math.ceil(3*this.deeptime);                                                
          }

      },
    showInfo:function(msg){
       this.dialogVisible = true;
       this.dialogMsg = msg

    },
    
    rnum:function(){   //
      var list =  this.$refs.task
  
      var num =0;
      var len = list.length

      for(var i=0;i<len;i++){
        num += list[i].num
      }
  
      this.totalnum = num 
    }
      
    }
  };

</script>

<style type="text/css">
.item{

text-align: center;
border-right:1px solid #eee;
border-bottom:1px solid #eee;

}
.header{
  background: #8feade;
  color: #fff;
}
.noborder input{
  border:0;
  text-align: center;
  padding:0;
}
.cf:before,.cf:after { content:""; display:table; } .cf:after { clear:both; }


</style>



