<template>
  <div>
      <el-button type="primary" style="margin-bottom:30px;" @click="refresh"></el-button>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="a"
      label="/id/id"
      width="200"
      >
    </el-table-column>
    <el-table-column
      prop="p"
      label="/">
    </el-table-column>
    <el-table-column
      prop="c"
      label="">
    </el-table-column>
      <el-table-column
      prop="m"
      label="">
    </el-table-column>
    <el-table-column
      prop="t"
      label="">
    </el-table-column>
     <el-table-column
      prop="b"
      label="">
    </el-table-column>
     <el-table-column
      prop="oper"
      label="">
    </el-table-column>
  </el-table>
  </div>
  
</template>

<script>
  export default {
    data() {
      return {
        tableData: ""
      }
    },
    created:function(){
       this.loaddata();
    },
    methods:{
      refresh:function(){
        this.loaddata();
      },
      loaddata:function(){
         const loading = this.$loading({    // loading
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
         this.$http.post("/customer/order/orderlist", {tel:"123"}).then(response => {
            var res = response.body;
             loading.close();
             if(res.status==1){
              this.$message('');
               this.tableData = res.data;
             }else{
              this.$message({message:res,type:"error"});
             }
         }).catch(() => {
          loading.close();         
        });
      }
    }

  }
</script>


